<template>
  <div>
    <v-container class="formulario-terapia-bilingue">
      <title-ribbon
        :title="this.$t('servicios.terapia-bilingue.formulario.titulo')"
      />
      <div class="formulario-terapia-bilingue__main">
        <v-row>
          <v-col>
            <validation-observer
              ref="observer"
              v-slot="{ invalid }"
            >
              <form
                class="px-0 mx-0 my-0"
                @submit.prevent="validateForm()"
              >
                <v-row class="formulario-terapia-bilingue_row__form">
                  <v-col>
                    <v-row class="formulario-terapia-bilingue_row_form_main">
                      <v-col class="col-12 col-md-6 crt-form__col">
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required|max:90"
                          name="Nombres:"
                        >
                          <label class="input-label">
                            {{ $t(i18_drawer + 'nombre') }}:
                          </label>
                          <v-text-field
                            v-model="nombres"
                            :error-messages="errors"
                            color="success"
                            filled
                            dense
                          />
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required|max:90"
                          name="Apellidos"
                        >
                          <label class="input-label">
                            {{ $t(i18_drawer + 'apellido') }}:
                          </label>
                          <v-text-field
                            v-model="apellidos"
                            :error-messages="errors"
                            color="success"
                            filled
                            dense
                          />
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required"
                          name="Genero"
                        >
                          <label class="input-label">
                            {{ $t(i18_drawer + 'genero.titulo') }}:
                          </label>
                          <v-radio-group
                            v-model="sexo"
                            hide-details
                            :error-messages="errors"
                            class="radio-genero"
                          >
                            <v-radio
                              v-for="(item, i) in sexos"
                              :key="i"
                              :label="item.text"
                              :value="item.id"
                              color="success"
                            />
                          </v-radio-group>
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required"
                          name="Especialidades - Lenguajes"
                        >
                          <label class="input-label">
                            {{
                              $t(i18_drawer + 'especialidad-lenguaje.titulo')
                            }}
                          </label>
                          <v-select
                            v-model="especialidad_lenguaje"
                            :error-messages="errors"
                            :items="especialidades"
                            item-text="NombreCompleto"
                            item-value="NombreCompleto"
                            label="Especialidades - Lenguajes"
                            persistent-hint
                            filled
                            dense
                            dark
                          />
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required|max:50|numeric"
                          name="Telefono"
                        >
                          <label class="input-label">
                            {{ $t(i18_drawer + 'telefono') }}
                          </label>
                          <v-text-field
                            v-model="telefono"
                            :error-messages="errors"
                            color="success"
                            filled
                            dense
                          />
                        </validation-provider>
                      </v-col>
                      <v-col class="col-12 col-md-6 crt-form__col">
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required"
                          name="Pas de Nacimiento"
                        >
                          <label class="input-label">
                            {{ $t(i18_drawer + 'pais-de-nacimiento') }}
                          </label>
                          <v-select
                            v-model="pais_nacimiento"
                            :error-messages="errors"
                            :items="paises"
                            item-text="NombreIngles"
                            item-value="NombreIngles"
                            label="Pas de Nacimiento"
                            persistent-hint
                            filled
                            dense
                            light
                          />
                        </validation-provider>
                        <v-radio-group
                          v-model="tipo_documento"
                          hide-details
                          class="radio-documento"
                        >
                          <v-radio
                            v-for="(glosa, i) in glosas_identificacion"
                            :key="i"
                            :label="glosa.text"
                            :value="glosa.id"
                            color="success"
                          />
                        </v-radio-group>
                        <validation-provider
                          v-slot="{ errors }"
                          :rules="
                            tipo_documento === 1
                              ? 'required|rut'
                              : 'required|max:50'
                          "
                          name="Rut/Pasaporte"
                        >
                          <v-text-field
                            v-model="documento"
                            :error-messages="errors"
                            color="success"
                            filled
                            dense
                            :validate-on-blur="false"
                            name="user[rut]"
                            flat
                          />
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required"
                          name="Fecha de Nacimiento"
                        >
                          <v-menu
                            ref="menuFnac"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="zoom-center-transition"
                            offset-y
                            min-width="auto"
                          >
                            <template v-slot:activator="{ on }">
                              <label class="input-label">
                                {{ $t(i18_drawer + 'fecha-de-nacimiento') }}
                              </label>
                              <v-text-field
                                v-model="computedOrdenarFecha"
                                color="success"
                                name="date"
                                readonly
                                :error-messages="errors[0]"
                                filled
                                dense
                                v-on="on"
                              />
                            </template>
                            <v-date-picker
                              v-model="fecha_nacimiento"
                              locale="es-ES"
                              color="success"
                              @input="menuFnac = false"
                            />
                          </v-menu>
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required|email|max:100"
                          name="E-Mail"
                        >
                          <label class="input-label">
                            E-Mail
                          </label>
                          <v-text-field
                            v-model="email"
                            :error-messages="errors"
                            color="success"
                            filled
                            dense
                          />
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          rules="max:500"
                          name="Comentarios"
                        >
                          <label class="input-label">
                            {{ $t(i18_drawer + 'comentarios') }}:
                          </label>
                          <v-textarea
                            v-model="comentarios"
                            :error-messages="errors"
                            filled
                            name="input-7-4"
                            color="success"
                          />
                        </validation-provider>
                        <div class="crt-form-bottom">
                          <div class="crt-form-bottom__captcha">
                            <vue-recaptcha
                              sitekey="6Lf7HQQeAAAAAC7MOA15SB35MQ8UwUh8wYG5rg-h"
                              @verify="onVerify"
                              @expired="onExpired"
                            />
                          </div>
                          <div class="crt-form-bottom__btn">
                            <v-btn
                              color="success"
                              default
                              type="submit"
                              :disabled="invalid"
                              :loading="btnLoader"
                            >
                              {{ $t(i18_drawer + 'btn-enviar') }}
                            </v-btn>
                            <fade-transition>
                              <div class="captcha-alert">
                                <v-alert
                                  v-show="showRecaptchaAlert"
                                  dense
                                  text
                                  type="warning"
                                  class="box"
                                >
                                  {{ $t('captcha-completar') }}
                                </v-alert>
                              </div>
                            </fade-transition>
                          </div>
                        </div>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
                <splitter-volver v-bind="{ text: `${$t('volver')}` }" />
              </form>
            </validation-observer>
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  import VueRecaptcha from 'vue-recaptcha'
  import { FadeTransition } from 'vue2-transitions'
  export default {
    name: 'FormularioTerapiaBilingue',
    components: {
      VueRecaptcha,
      FadeTransition,
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
    },
    data () {
      return {
        i18_drawer: 'servicios.terapia-bilingue.formulario.',
        nombres: '',
        apellidos: '',
        sexo: '',
        documento: '',
        tipo_documento: 1,
        fecha_nacimiento: '',
        pais_nacimiento: '',
        email: '',
        telefono: '',
        especialidad_lenguaje: '',
        comentarios: '',
        cod_sucursal: this.$route.params.sucursal,
        locale: this.$route.params.locale,
        paises: [],
        especialidades: [],
        especialidades_lenguajes: [
          {
            id: 1,
            text: this.$t(
              'servicios.terapia-bilingue.formulario.especialidad-lenguaje.opciones.psicologia-ingles',
            ),
          },
          {
            id: 2,
            text: this.$t(
              'servicios.terapia-bilingue.formulario.especialidad-lenguaje.opciones.psicologia-portugues',
            ),
          },
        ],
        glosas_identificacion: [
          { id: 1, text: this.$t('servicios.terapia-bilingue.formulario.rut') },
          {
            id: 2,
            text: this.$t('servicios.terapia-bilingue.formulario.pasaporte'),
          },
        ],
        sexos: [
          {
            id: 0,
            text: this.$t(
              'servicios.terapia-bilingue.formulario.genero.opciones.femenino',
            ),
          },
          {
            id: 1,
            text: this.$t(
              'servicios.terapia-bilingue.formulario.genero.opciones.masculino',
            ),
          },
        ],
        //
        charged: false,
        btnLoader: false,
        showRecaptchaAlert: false,
        recaptchaVerified: false,
      }
    },
    computed: {
      computedOrdenarFecha () {
        return this.ordenarFecha(this.fecha_nacimiento)
      },
    },
    watch: {
      recaptchaVerified: function () {
        if (this.recaptchaVerified) {
          this.hideMsg()
        }
      },
      documento: function () {
        if (this.tipo_documento === 1) {
          this.documento = this.documento
            .replace(/^0+|[^0-9kK]+/g, '')
            .toUpperCase()
          const len = this.documento.length - 1
          if (len > 3) {
            const dv = this.documento[len]
            const beforeDv = this.documento
              .substring(0, len)
              .replace(/\B(?=(\d{3})+(?!\d))/g, '.')
            this.documento = beforeDv + '-' + dv
          }
        }
      },
    },
    created () {
      this.$vuetify.rtl = false
      this.$i18n.locale = this.$route.params.locale
      this.getPaises()
      this.getEspecialidades(this.$route.params.locale)
    },
    methods: {
      getPaises () {
        Vue.axios
          .get('/api/paises')
          .then(res => {
            this.paises = res.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      getEspecialidades () {
        Vue.axios
          .post('/api/especialidades/bilingue', {
            cod_sucursal: 1,
            locale: this.$route.params.locale,
          })
          .then(res => {
            this.especialidades = res.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      ordenarFecha (fecha) {
        if (!fecha) return null

        const [year, month, day] = fecha.split('-')
        return `${day}/${month}/${year}`
      },
      onVerify () {
        if (!this.recaptchaVerified) {
          this.recaptchaVerified = true
        }
      },
      onExpired () {
        this.recaptchaVerified = false
      },
      hideMsg () {
        this.showRecaptchaAlert = false
      },
      limpiarFormulario () {
        this.documento = ''
        this.nombres = ''
        this.apellidos = ''
        this.sexo = null
        this.fecha_nacimiento = ''
        this.especialidad_lenguaje = ''
        this.pais_nacimiento = ''
        this.telefono = ''
        this.email = ''
        this.comentarios = ''
        this.tipo_documento = ''
        requestAnimationFrame(() => {
          // Reseteamos vee
          this.$refs.observer.reset()
        })
        this.onExpired() // Reseteamos captcha
      },
      async validateForm () {
        const isValid = await this.$refs.observer.validate()
        if (!isValid) {
          return false
        }

        if (!this.recaptchaVerified) {
          this.showRecaptchaAlert = true
          return false
        }

        // console.log('validando')
        this.btnLoader = true

        const body = {
          cod_sucursal: this.cod_sucursal,
          locale: this.locale,
          documento: this.documento,
          nombres: this.nombres,
          apellidos: this.apellidos,
          sexo: this.sexo,
          fecha_nacimiento: this.fecha_nacimiento,
          especialidad_lenguaje: this.especialidad_lenguaje,
          pais_nacimiento: this.pais_nacimiento,
          telefono: this.telefono,
          email: this.email,
          comentarios: this.comentarios,
          tipo_documento: this.tipo_documento,
        }

        Vue.axios
          .post('/api/formularios/terapia-bilingue', body)
          .then(response => {
            this.limpiarFormulario()
            this.btnLoader = false
            this.$swal({
              icon: 'success',
              title: this.$t('servicios.terapia-bilingue.formulario.exito'),
              text: this.$t(
                'servicios.terapia-bilingue.formulario.envio-exitoso',
              ),
            })
          })
          .catch(error => {
            console.log(error.message)
          })
      },
    },
  }
</script>
